/*============CATEGORÍAS===========*/
.container {
  padding-top: 4rem;
  padding-bottom: 4rem;
  max-width: 1400px;
  margin: 0 auto;
  color: #164d6c;
}

/* Título principal */
.titulo-categorias {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 3rem;
  color: #2c8ead;
  position: relative;
  letter-spacing: 0.05em;
}

.titulo-categorias::after {
  content: "";
  display: block;
  width: 80px;
  height: 6px;
  background-color: #2c8ead;
  margin: 1rem auto 0;
  border-radius: 3px;
}

/* Buscador */
input.form-control {
  padding: 0.75rem 1rem;
  border-radius: 10px;
  border: 2px solid #2c8ead;
  font-size: 1rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  margin-bottom: 2rem;
}

input.form-control:focus {
  border-color: #164d6c;
  box-shadow: 0 0 0 0.2rem rgba(22, 77, 108, 0.25);
}

/* Tarjetas de categorías */
.card {
  background: transparent;
  border: 2px solid #2c8ead;
  border-radius: 16px;
  overflow: hidden;
  transition: transform 0.35s ease, box-shadow 0.35s ease;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.card:hover {
  transform: translateY(-12px);
  box-shadow: 0 12px 28px rgba(44, 142, 173, 0.3);
  border-color: #164d6c;
  background: #f0f8fc;
}

/* Imagen */
.card-img-top {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 2px solid #2c8ead;
  transition: transform 0.3s ease;
}

.card:hover .card-img-top {
  transform: scale(1.07);
}

/* Cuerpo de la tarjeta */
.card-body {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.card-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #164d6c;
  margin-bottom: 12px;
}

/* Responsive Grid (si usas Bootstrap o CSS Grid) */
@media (max-width: 900px) {
  .container {
    padding: 3rem 1.5rem;
  }

  .titulo-categorias {
    font-size: 2.5rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 2.5rem 1rem;
  }

  .titulo-categorias {
    font-size: 2rem;
  }

  .card-img-top {
    height: 160px;
  }
}

/* Enlaces sin subrayado */
a.text-decoration-none {
  text-decoration: none !important;
}
